
.uw_application {
  &--container {
    & > .row, & > .row > .col {
      margin: 0px;
      padding: 0px; } }


  &--side {
    height: 100%;
    background: radial-gradient($sideBg, darken($sideBg, 5%)) !important;
    box-shadow: 0px 1px 1px #444;
    .logo {
      text-align: left;
      color : $sideActiveColor;
      font-size: 22px;
      font-weight: bold;
      padding: 20px 0px 30px 20px; }
    .collection {
      border: none;
      padding: 0px;
      margin: 0px; }
    li.collection-item {
      border: none;
      background: transparent !important;
      padding: 0px;
      cursor: default;
      & > a {
        overflow: hidden;
        height: 114px;
        display: block;
        padding: 20px;
        &.disabled {
          cursor: default; } }
      .control {
        div {
          color: darken($sideActiveColor, 50%); }
        border-bottom: 1px solid #888;
        span {
          font-size: 22px; }

        i {
          font-size: 22px;
          margin-right: 5px; } }
      .description {
        padding-top: 5px;
        color: darken($sideActiveColor, 50%); }
      .enabled {
        display: block;
        .control {
          div {
            font-weight: bold;
            color: $sideActiveColor; } }

        .description {
          color: darken($sideActiveColor, 5%); } } }
    li.collection-item.active {
      background: darken($sideBg, 10%) !important;
      .control {
        border-bottom: 1px dashed $sideActiveColor; } } } }



